export class Goal {
    constructor(
        private id: string,
        private name: string
    ) {}

    public get Id(): string {
        return this.id;
    }

    public set Id(goalId: string) {
       this.id = goalId;
    }

    public get Name(): string {
        return this.name;
    }

    public set Name(goalName: string) {
       this.name = goalName;
    }

}