import { Component, OnInit } from '@angular/core';
import { Goal } from './goal';

@Component({
  selector: 'app-goal',
  templateUrl: './goal.component.html',
  styleUrls: ['./goal.component.scss']
})
export class GoalComponent implements OnInit {

  public goal: Goal = new Goal('a','');

  public isSaved: boolean = true;
  public isDone: boolean = false;

  constructor() { }

  ngOnInit(): void {
  }

  public save(goal: Goal): void {
    this.isSaved = true;
  }

  public edit(goal: Goal, goalInput?: HTMLElement): void {
    this.isSaved = false;
    if (goalInput){
      goalInput.focus();
    }
  }

  public done(goal: Goal): void {
    this.isDone = true;
  }

  public delete(): void {
   //todo: add delete
  }

  public canSave(): boolean {
    return this.goal 
      && Boolean(this.goal.Name) && !this.isSaved;
  }

  public canDone(): boolean {
    return this.goal 
      && Boolean(this.goal.Name) && this.isSaved;
  }

  public makeUnmarkGoal(): void {
    this.isDone = false;
  }

}
